import React, { Component } from 'react';
import FontAwesome from 'react-fontawesome';
import './style.sass';
class Star extends Component {
    constructor(props) {
        super(props);
        this.template = this.template.bind(this);
        this.state = {
            max: 5
        }
    }

    template() {
        let html = [];
        for (let i = 1; i <= this.props.num; i++) {
            html.push(<FontAwesome key={i} className="star red-star" name="star"/>);
        }
        for (let i = 0; i < this.state.max - this.props.num; i++) {
            html.push(<FontAwesome key={this.state.max - i} className="star-o" name="star-o"/>);
        }
        // console.log(html);
        return html;
    }

    render() {
        return (
            <div className="star-list">
                 {this.template()}
            </div>
        );
    }
}

export default Star;


