import * as cot from '../constants/collect';
import localStore from '../util/localStore';
const collectInfo = (state = [],action) => {
    switch(action.type) {
        case cot.ADD : 
            state.push({shopId: action.shopId});
            saveState(state);
            return state; break;
        case cot.DELETE : 
            // console.log(action);
            let colIndex = 0;
            state.map((item,index) => {
                if(item.shopId == action.shopId) {
                    colIndex = index;
                    return;
                }
            })
            //  console.log("colIndex = " + colIndex);
            state.splice(colIndex,1);
            saveState(state);
            return state;
        default: return state;break;
    }
}

function saveState(state) {
    localStore.setItem("collectInfo",JSON.stringify(state));
}


export {collectInfo}


