import React, { Component } from 'react';
import {svnResource} from '../../util/publicPath';

class Img extends Component {
    constructor(props) {
        super(props);
        this.state = {
            src: "",
            alt: "",
        }
    }

    
    componentWillMount() {
        this.setState({
            src: this.props.src,
            alt: this.props.alt
        })
        // console.log("img");
        // console.log(this.state);
    }
    

    render() {
        const image = require("../../static/img/" + this.state.src);
        // console.log(image);
        return (
            <img src={image} alt={this.state.alt}/>
        );
    }
}

export default Img;


