import * as shopCar from '../constants/shopCar';
import localStore from '../util/localStore';

const shopCarInfo = (state = [],action) => {
    console.log("操作购物车中...");
    switch (action.type) {
        case shopCar.ADD : 
            let exist = false;
            state.map((item,index) => {
                if(item.shopId == action.shopId) {
                    exist = true;
                    item.num ++
                }
            });
            if(!exist){
                state.push({shopId:action.shopId,num: 1})
            }
        saveInfo(state);
        return state;
        case shopCar.CUT :
            let ind = 0;
            let del = false;
            state.map((item,index) => {
                if(item.shopId == action.shopId) {
                    item.num --;
                    if(item.num == 0) {
                        del = true;
                        ind = index;
                    }
                }
            })
            if(del) {
                state.splice(ind,1);
            }
            saveInfo(state);
        default : return state;
    }
}

function saveInfo(state) {
    localStore.setItem("shopCarInfo",JSON.stringify(state));
}


export {shopCarInfo}


