import React, { Component } from 'react';
import FontAwesome from 'react-fontawesome';
import Star from '../../Star';
class Item extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        const data = this.props.data;
        return (
            <div>
                <div>
                    <FontAwesome className="user-info" name="user-info"/>
                </div>
                <div><Star num={data.star}/></div>
                <div>
                    {data.comment}
                </div>
            </div>
        );
    }
}

export default Item;


