import React, { Component } from 'react';
import FontAwesome from 'react-fontawesome';
import './style.sass';
class Collect extends Component {
    constructor(props) {
        super(props);
    }

    
   



    render() {
        return (
            <div className="collect-div" onClick={this.props.collected ? this.props.del : this.props.add}>
                <div>
                    <FontAwesome className={this.props.collected ? "collected" : ""} name="star-o"/>
                </div>
                <div>
                    {this.props.collected ? "已收藏" : "收藏"}
                </div>
            </div>
        );
    }

}



export default  Collect;


