import React, { Component } from 'react';
import FontAwesome from 'react-fontawesome';
import './style.sass';
class Buy extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className="buy-module" onClick={this.props.add}>
                <div>
                    <FontAwesome name="cart-plus"/>
                </div>
                <div>
                    购买
                </div>
                {
                    this.props.buyNum > 0 ? <div> 已购买{this.props.buyNum}件 </div> : ""
                }
                
            </div>
        );
    }
}

export default Buy;


