import * as collect from '../constants/collect';

export function qry(shopId,userName) {
    return {
        type: collect.QRY,
        shopId,
        userName
    }
}

export function add(shopId,username) {
    return {
        type: collect.ADD,
        shopId,
        username
    }
}

export function del(shopId,username){
    return {
        type: collect.DELETE,
        shopId,
        username
    }
}

 


