import React, { Component } from 'react';
import {withRouter } from 'react-router-dom';
import FontAwesome from 'react-fontawesome';
import { Toast} from 'antd-mobile';
import localStore from '../../util/localStore';
import * as url from '../../constants/url';
import './style.sass';
class LoginComp extends Component {
    constructor(props) {
        super(props);
        this.login = this.login.bind(this);
        this.inputEnter = this.inputEnter.bind(this);
        this.state = {
            username: "",
            password: ""
        }
    }


    login(){
        if(this.state.username.trim().length === 0) {
            Toast.fail("请输入用户名");
        }else if (this.state.password.trim().length === 0) {
            Toast.fail("请输入密码");
        }else {
            Toast.loading("登录中...",0);
            setTimeout(()=>{
                this.props.updateUserInfo({username: this.state.username});
                Toast.hide();
                Toast.success("登录成功",3,()=>{
                    this.props.history.push(url.Home);
                })
            },3000);
        }
    }

    inputEnter(e) {
        const keyCode = e.keyCode;
        const className = e.target.className;
        // console.log(className);
        if(keyCode == 13) {
            if(className == "username") {
                this.refs.password.focus();
            } else if(className == "password") {
                this.refs.password.blur();
                this.refs.submit.click();
            }
        }else {
            if(className == "username") {
                this.setState({
                    username: e.target.value
                })
            } else if(className == "password") {
                this.setState({
                    password: e.target.value
                })
            }
        }
    }


    render() {
        return (
            <div className="login-module">
                <div className="input-group">
                    <FontAwesome className="icon" name="user-o"/>
                    <input type="text" className="username" placeholder="用户名(随便输入)" onKeyUp={this.inputEnter}/>
                </div>
                <div className="input-group">
                    <FontAwesome className="icon" name="key"/>
                    <input ref="password" type="password" className="password" placeholder="密码(随便输入)" onKeyUp={this.inputEnter}/>
                </div>
                <div>
                    <button ref="submit" type="button" className="login-btn" onClick={this.login}>登录</button>
                </div>
            </div>
        );
    }
}

export default withRouter(LoginComp);


